
body {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    background-color: #FAFAFA;
    min-height: 100vh;
}
#app-main {
    width: 100%;
    height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    position: absolute;
}
ul {
  list-style: none;
}
.header {
    height: 10vh;
    width: 100%;
    color: #000066;
    border-bottom: 1px solid #000066;
    overflow: hidden;
    position: absolute;
}
.header img {
    position: relative;
    width: 10%;
    overflow: hidden;
    left: 20%;
    top: 20%;
}
.footer {
    height: 6vh;
    width: 100%;
    color: #b8b8b8;
    border-top: 1px solid #000066;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.footer p {
    position: absolute;
    margin: 0;
    left: 10%;
    bottom: 0;
    font-size: 13px;
}


